<template>
  <component v-bind:is="layout"/>
</template>

<script>
import Default from "@/layouts/Default.vue";
import Admin from "@/layouts/Admin.vue";

export default {
  name: 'App',
  components: {
    Default,
    Admin
  },
  computed: {
    layout(){
      return this.$route.meta.layout || 'default'
    }
  },
  async created(){
    let vm = this;
    await vm.$store.dispatch('News/setup');
  },
  mounted(){
    console.log('mount app')
  }
};
</script>