<template>
  <div class="masonry-wrap text-center">
    <div class="masonry" v-if="item">
      <div class="brick" v-for="(v, k) in item" :key="k" @click="onClick(v.key)">
        <template v-if="v.pics && v.pics.length">
          <img v-lazy="v.pics[0].src" :alt="v.pics[0].alt" :title="v.title">
          <h2>{{v.title}}</h2>
        </template>
      </div>
    </div>
    <button class="box-button" @click="loadData" v-if="more">Xem Thêm</button>
  </div>
</template>

<script>
export default {
  name: 'masonry',
  props: ['item', 'more'],
  methods: {
    onClick(key){
      this.$emit('onClick', key);
    },
    loadData(){
      let vm = this;
      let cat = vm.item[0].cat || 'trending';
      let offset = vm.item.length;
      let limit = 50;
      vm.$store.dispatch('News/moreByCat', {cat: cat, limit: limit, offset: offset});
    }
  },
}
</script>