import {SHEET_LINK, COOKIE} from "@/utils/constants";
import {removeUnicodeSpace} from "@/utils/helper";
import API from "./news.api";
import Fuse from 'fuse.js'
import _ from "lodash/array";

async function ajax (opt) {
  let data;
  try {
    data = await API[opt.name](opt);
  } catch (err) {
    return
  }
  return data
};

//get tab active
import Cookies from 'js-cookie';
let cat = Cookies.get('cl_tab') || 'trending';
if(!cat || cat == 'undefined'){
  cat = 'trending'
}

//Khởi tạo state mặc định
const initialState = () => ({
  all: [],
  search: [],
  records:[],
  record: {}
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();


/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  all: state => state.all,
  search: state => state.search,
  records: state => state.records,
  record: state => state.record,
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET (state, data) {
    state.records = data;
    state.all = _.unionBy(state.all, data, 'key'); // all data: merge and filter array 
  },
  FIND (state, data) {
    let result = state.records.find((record) => record.key === data);
    state.record = {};
    if(result){
      state.record = result
      return true
    }
    return false;
  },
  SEARCH(state, data){
    const options = {
      shouldSort: true,
      threshold: 0.5,
      location: 0,
      distance: 50, //Xác định mức độ trùng khớp phải ở vị trí tìm thấy ký tự 
      minMatchCharLength: 2,
      findAllMatches: true,
      keys: [
        "title",
        "description"
      ]
    }
    
    const fuse = new Fuse(state.all, options);

    //extend search join 2 char 'Man 'Old
    let results = fuse.search(data);
    if(results && results.length){
      let output = [];
      for (const it of results) {
        output.push(it.item)
      }
      state.search = output
    }
    else{
      state.search = []
    }

  },
  ADD (state) {
    state.records.push(state.record)
  },
  EDIT (state, data) {
    let records = state.records
    records.splice(records.indexOf(data), 1)
    state.records = records
    state.record = data
  },
  REMOVE (state, data) {
    let records = state.records
    records.splice(records.indexOf(data), 1)
  },
  CLEAN (state) {
    state.record = {}
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}
/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setup: async ({commit}, opt) => {
    let data;
    try {
      if(opt && opt.cat){
        cat = opt.cat
      }
      data = await API.GET_LIMIT({url: SHEET_LINK[cat], limit: 50});
    } catch (err) {
      return
    }
    commit('SET', data)
  },
  find: async ({commit, state}, data) => {
    commit('FIND', data);

    if(!state.record.key){
      let result;
      try {
        result = await API.GET({url: SHEET_LINK[cat], key: data});
      } catch (err) {
        return
      }
      state.record = result;

      state.all = _.unionBy(state.all, [result], 'key'); // all data: merge and filter array 
    }
  },
  filterByCat: async ({commit, state}, opt) => {

    Cookies.set('cl_tab', opt.cat, { expires: 7 });

    let result = state.all.filter(function (item) {
      return item.cat === opt.cat;
    });

    if(result.length < 1){
      try {
        result = await API.GET_LIMIT({url: SHEET_LINK[opt.cat], limit: 50, offset: opt.offset || 0});

      } catch (err) {
        console.log(err)
        return
      }

      state.all = _.unionBy(state.all, result, 'key'); // all data: merge and filter array 

    console.log(state.all)
    }

    state.records = result;
    
  },
  moreByCat: async ({commit, state}, opt) => {

    Cookies.set('cl_tab', opt.cat, { expires: 7 });

    let result = state.all.filter(function (item) {
      return item.cat === opt.cat;
    });

    //more
    let offset = opt.offset || 0;

    if(result.length < 1 || offset > 0){
      try {
        result = await API.GET_LIMIT({url: SHEET_LINK[opt.cat], limit: 20, offset: opt.offset || 0});

      } catch (err) {
        console.log(err)
        return
      }

      state.all = _.unionBy(state.all, result, 'key'); // all data: merge and filter array 

      state.records = state.records.concat(result); // merge array 
    }
    
  },
  add: async ({commit}) => {
    commit('ADD')
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    commit('REMOVE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}