<template>
  <div id="app">
    
    <!-- header -->
    <header id="header">
      <section class="cover container">
        <a id="back" title="back" v-if="isSearch" @click="backLink"></a>
        <a href="/" id="logo">
          <img src="/img/icons/logo.png" >
          <span>Xu Hướng</span>
        </a>
        <div id="search-form">
          <button><img src="/img/icons/search.svg"></button>
          <input type="search" placeholder="Search.." name="search" v-model="search">
        </div>
      </section>
    </header>

    <main id="main">
      <transition :name="transitionName">
        <router-view/>
      </transition>
    </main>
    <!-- footer -->
    <footer id="footer">

    </footer>
  </div>
</template>
<script>
import _ from 'lodash/function';
import Default from "@/layouts/Default.vue";
import Admin from "@/layouts/Admin.vue";
export default {
  data() {
    return {
      transitionName: 'slide',
      search: '',
      isSearch: false
    };
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
    },
    'search': _.debounce(function(val) {
      let vm = this;
      vm.$root.$emit('searchEvent' , val);
      if(val){
        vm.isSearch = true;
      }
      else{
        vm.isSearch = false;
      }
    }, 800)
  },
  methods: {
    beforeLeave(element) {
      this.prevHeight = getComputedStyle(element).height;
    },
    enter(element) {
      const { height } = getComputedStyle(element);

      element.style.height = this.prevHeight;

      setTimeout(() => {
        element.style.height = height;
      });
    },
    afterEnter(element) {
      element.style.height = 'auto';
    },
    backLink(){
      let vm = this;
      vm.search = '';
      vm.$root.$emit('searchEvent' , '');
      vm.isSearch = false
    }
  },
  created() {
   this.$router.beforeEach((to, from, next) => {
      let transitionName = 'slide';
      const toDepth = to.path.split('/').length;
      const fromDepth = from.path.split('/').length;
      transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left';
    next();
   });
   
 },
}
</script>
<style lang="scss" src="@/assets/css/custom.scss"></style>