<template>
  <div id="home-page">

    <section class="container" v-if="!isSearch">
      <div id="filter-wrap">
        <a class="filter" :class="{'active': cat === 'trending'}" @click="onFilter('trending')">Xu hướng</a>
        <a class="filter" :class="{'active': cat === 'tech'}" @click="onFilter('tech')">Công Nghệ</a>
        <a class="filter" :class="{'active': cat === 'health'}" @click="onFilter('health')">Sức Khoẻ</a>
        <a class="filter" :class="{'active': cat === 'foodTravel'}" @click="onFilter('foodTravel')">Du Lịch & Ẩm Thực</a>
      </div>
    </section>

    <section class="container" v-if="!isSearch">
      <Masonry :item="newses" @onClick="popupOpen" :more="true"/>
    </section>

    <section class="container" v-if="isSearch">
      <Masonry :item="search" @onClick="goLink"/>
    </section>
    
    <div id="popup-wrap" :class="{'active': popup}">
      <div id="popup-overlay" @click="popupClose"></div>
      <Info id="popup" :item="news" @popupClose="popupClose">
        <div class="box">
          <div class="box-header">
            <h2>Bài viết theo chuyên mục</h2>
          </div>
          <div class="box-body">
            <Masonry :item="newses" @onClick="goLink"/>
          </div>
        </div>
      </Info>
    </div>
    
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import Masonry from "@/components/Masonry.vue";
import Info from "@/components/Info.vue";

export default {
  name: 'home-page',
  components: {
    Info,
    Masonry
  },
  data() {
    return {
      isSearch: false,
      popup: false,
      cat: Cookies.get('cl_tab') || 'trending',
    }
  },
  computed: {
    news: function (){
      return this.$store.getters['News/record']
    },
    newses: function (){
      return this.$store.getters['News/records']
    },
    search: function (){
      return this.$store.getters['News/search']
    },
  },
  methods: {
    onFilter(cate){
      this.cat = cate;
      this.$store.dispatch('News/filterByCat', {cat: cate});
    },
    popupOpen(key){
      let vm = this;
      vm.popup = true;
      vm.$store.dispatch('News/find', key);
      // if(history) history.pushState({}, 'd'+key, '/#/d/'+key)
    },
    popupClose(){
      this.popup = false;
      // if(history) history.go(-1)
    },
    goLink(key){
      let vm = this;
      vm.popup = true;
      vm.$router.push('/d/'+key);
    },
  },
  mounted(){
    let vm = this;
    vm.$root.$on('searchEvent', (data) => { 
      if(data){
        vm.$store.commit('News/SEARCH', data);
        vm.isSearch = true;
      }
      else{
        vm.isSearch = false;
      }
    });
  }
};
</script>
