<template>
  <div id="detail-page">

    <section class="container">
      <div id="filter-wrap">
        <a class="filter" :class="{'active': cat === 'trending'}" @click="onFilter('trending')">Xu hướng</a>
        <a class="filter" :class="{'active': cat === 'tech'}" @click="onFilter('tech')">Công Nghệ</a>
        <a class="filter" :class="{'active': cat === 'health'}" @click="onFilter('health')">Sức Khoẻ</a>
        <a class="filter" :class="{'active': cat === 'foodTravel'}" @click="onFilter('foodTravel')">Du Lịch & Ẩm Thực</a>
      </div>
    </section>

    <Info id="popup" :item="news" :noback="true">
      <div class="box" v-if="newses">
        <div class="box-header">
          <h2>Bài viết theo chuyên mục</h2>
        </div>
        <div class="box-body" v-viewability="{padded: 100, store: {dispatch: 'News/setup'}, callback:viewedLoad }">
          <Masonry :item="newses" @onClick="goLink" :more="true"/>
        </div>
      </div>
    </Info>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import Masonry from "@/components/Masonry.vue";
import Info from "@/components/Info.vue";

export default {
  name: 'detail-page',
  components:{
    Info,
    Masonry
  },
  data() {
    return {
      key: null,
      cat: Cookies.get('cl_tab') || 'trending',
    };
  },
  computed: {
    news(){
      return this.$store.getters['News/record'];
    },
    newses: function (){
      return this.$store.getters['News/records'];
    },
  },
  methods: {
    onFilter(cate){
      this.cat = cate;
      this.$store.dispatch('News/filterByCat', {cat: cate});
      this.$router.push('/');
    },
    popupClose(){
      this.$router.back();
    },
    goLink(key){
      let vm = this;
      vm.$router.push('/d/'+key);
    },
    viewedLoad(store){
      let vm = this;
      if(!vm.newses.length){
        vm.$store.dispatch(store.dispatch, vm.news.cat);
      }
    }
  },
  async created(){
    let vm = this;
    vm.key = vm.$route.params.key;
    await vm.$store.dispatch('News/find', vm.key);

  }
};
</script>